div.broken,
div.scattered,
div.few {
  background-color: yellow;
  height: 100vh;
  width: 100vw;
  background: url(/src/images/few_clouds.jpg) no-repeat center center fixed;
  background-size: cover;
  color: white;
}

div.overcast {
  width: 100vw;
  height: 100vh;
  background-color: darkcyan;
  top: 0;
  left: 0;
  background: url(/src/images/overcast_clouds.jpg) no-repeat center center fixed;
  background-size: cover;
  color: white;
}

